import React from "react";
import icMD from "../../icons/info/icMD.svg";
import Star from "./Stars";

function Rattings({ review }) {
  const rate = 4.5;
  return (
    <div className="ratings-container">
      <div className="star-container">
        <p className="rate">{rate}</p>
        <Star rate={rate} />
      </div>
      <div className="reviews-container">
        <div className="reviews">based on {review} reviews</div>
        <img src={icMD} alt="info" className="icMD" />
      </div>
    </div>
  );
}

export default Rattings;



